import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";

const Breadcrumb = () => {
  const navigate = useNavigate();
  const breadcrumb = useSelector(state => state.layout.details)

  useEffect(() => {
    console.log(breadcrumb)
  }, [breadcrumb])

  return (
    <>
      {breadcrumb && <div className="flex mt-8 text-xs">
        <div className="">
          <span className="font-semibold text-5xl text-white">{ breadcrumb.title }</span>
        </div>
        {/* <i className="fa-solid fa-house" /> */}
      </div>}
    </>
  )
}

export default Breadcrumb;