import { memo } from 'react';
import './zui.scss';
import Button from './Button/Button';
import Input from './Input/Input';
import { Select, Option } from './Select/Select';
import Menu from './Menu/Menu';
import Icon from './Icon/Icon';

const ZUI = (props) => {
  return props.children
}

const Row = memo(function Row({
  children,
  className
}) {
  return <div className={[
    "w-full",
    className ? className : ""
  ].join(" ").replace(/ +/g, ' ')}>
    {children}
  </div>
})

const Container = memo(function Container({
  children,
  className
}) {
  return (
    <div className={[
      "flex w-full max-w-7xl px-4 mx-auto",
      className ? className : ""
    ].join(" ").replace(/ +/g, ' ')}>
      {children}
    </div>
  )
})

ZUI.Button = Button;
ZUI.Icon = Icon;
ZUI.Input = Input;
ZUI.Menu = Menu;
ZUI.Select = Select;
ZUI.Option = Option;
ZUI.Row = Row;
ZUI.Container = Container;

export default ZUI;
