import { Link } from "react-router-dom";
import ZUI from "../../components/zui/zui";
import { subscriptions } from "../Dashboard/DashboardPage";
import { useEffect } from "react";
import { setDetails } from "../../store/reducers/layoutSlice";
import { useDispatch } from "react-redux";

const SubscriptionsPage = ({
  routeId,
  routePathId,
  routeTitle
}) => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(setDetails({
      id: routeId,
      pathID: routePathId,
      title: routeTitle
    }))
  }, [])

  const mapSubscriptions = (subs) => {
    return subs.map((sub, index) => {
      return (
        <div key={index} className="relative w-full pt-4 pb-6 rounded-2xl bg-red-200 overflow-hidden">
          <Link to={sub.sub_id.toLowerCase()}>
            <div className="absolute left-0 top-0 flex justify-center items-center w-full h-full bg-black/60 opacity-0 hover:opacity-100 duration-100 z-[2] overflow-hidden">
              <ZUI.Button primary roundedFull>
                Manage
              </ZUI.Button>
            </div>
          </Link>
          <div className="px-6 py-4 relative">
            <div className="absolute left-[-1.25rem] top-0 w-8 h-full rounded-r-xl bg-primary" />
            <div className="font-semibold text-stone-800 text-2xl leading-none">{sub.sub_id}</div>
          </div>
          <div className="px-6">
            <div className="py-2">
              <div className="font-semibold text-xs text-stone-600 leading-none">Plan Name</div>
              <div className="font-semibold text-stone-800 leading-none">{sub.plan_name}</div>
            </div>
            <div className="py-2">
              <div className="font-semibold text-xs text-stone-600 leading-none">Service Address</div>
              <div className="font-semibold text-stone-800 leading-none">{sub.service_address}</div>
            </div>
            <div className="py-2">
              <div className="font-semibold text-xs text-stone-600 leading-none">End Date</div>
              <div className="font-semibold text-stone-800 leading-none">{sub.end_date}</div>
            </div>
          </div>
        </div>
      )
    })
  }

  return (
    <div className="subscriptions-page w-full h-full">
      <ZUI.Row>
        <ZUI.Container>
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 w-full">
            {mapSubscriptions(subscriptions)}
          </div>
        </ZUI.Container>
      </ZUI.Row>
    </div>
  )
}

export default SubscriptionsPage;