import "./LoginForm.scss";

import { useState } from "react";
import ZUI from "../../../components/zui/zui";
import { Tooltip } from "react-tooltip";
import notify from "../../../assets/utils/notification";
import { useNavigate } from "react-router-dom";

const LoginForm = ({
  className,
  ipLocation
}) => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    "account-number": 'T00001234',
    password: 'password',
  })
  const [loginPending, setLoginPending] = useState(false);

  const handleLogin = (e) => {
    e.preventDefault();
    setLoginPending(true);
    setTimeout(() => {
      notify.success('Welcome!', {
        onOpen: () => navigate(`/${ipLocation}/dashboard`)
      })
    }, 1234)
  }

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData({ ...formData, [id]: value })
  }

  return (
    <form className={["w-full max-w-xs p-6 rounded-2xl bg-white", className].join(" ")} onSubmit={ handleLogin }>
      <div className="mb-4 relative">
        <ZUI.Input type="text" id="account-number" label="Account Number" value={ formData["account-number"] } onChange={ handleChange } uppercase required disabled={ loginPending } />
        <div id="account-number-tooltip" className="absolute right-4 top-1/4 z-50">
          <span className="opacity-50"><i className="fa-solid fa-circle-info"></i></span>
        </div>
        <Tooltip anchorSelect="#account-number-tooltip" place="bottom" content="Lorem ipsum dolor sit amet" />
      </div>
      <div className="mb-4">
        <ZUI.Input type="password" id="password" label="Password" value={ formData.password } onChange={ handleChange } required disabled={ loginPending } />
      </div>
      <div>
        <ZUI.Button type="submit" className="group" primary full disabled={ loginPending }>
          <span className="relative">
          { loginPending ?
            <i className="fa-solid fa-spinner fa-spin"></i> :
            <>
              <span className="absolute left-[-24px]">
                <svg xmlns="http://www.w3.org/2000/svg" height="18" width="18" viewBox="0 0 512 512" className="fill-white">
                  <path id="path1" d="M217.9 105.9L340.7 228.7c7.2 7.2 11.3 17.1 11.3 27.3s-4.1 20.1-11.3 27.3L217.9 406.1c-6.4 6.4-15 9.9-24 9.9c-18.7 0-33.9-15.2-33.9-33.9l0-62.1L32 320c-17.7 0-32-14.3-32-32l0-64c0-17.7 14.3-32 32-32l128 0 0-62.1c0-18.7 15.2-33.9 33.9-33.9c9 0 17.6 3.6 24 9.9">
                  </path>
                  <path d="M352 416l64 0c17.7 0 32-14.3 32-32l0-256c0-17.7-14.3-32-32-32l-64 0c-17.7 0-32-14.3-32-32s14.3-32 32-32l64 0c53 0 96 43 96 96l0 256c0 53-43 96-96 96l-64 0c-17.7 0-32-14.3-32-32s14.3-32 32-32z"></path>
                </svg>
              </span>
              <span>Login</span>
            </>
          }
          </span>
        </ZUI.Button>
      </div>
    </form>
  )
}

export default LoginForm;