import { memo } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/css';
import { Pagination } from "swiper/modules";

const SubsSlider = memo(function SubsSlider({
  subscriptions,
}) {

  const mapSubscriptionCards = (items) => {
    return items.map((item, index) => <SwiperSlide key={index} className="w-full pt-4 pb-6 rounded-2xl bg-red-200 overflow-hidden" style={{ height:"unset" }}>
      <div className="px-6 py-4 relative">
            <div className="absolute left-[-1.25rem] top-0 w-8 h-full rounded-r-xl bg-primary" />
            <div className="font-semibold text-stone-800 text-2xl leading-none">{item.sub_id}</div>
          </div>
          <div className="px-6">
            <div className="py-2">
              <div className="font-semibold text-xs text-stone-600 leading-none">Plan Name</div>
              <div className="font-semibold text-stone-800 leading-none">{item.plan_name}</div>
            </div>
            <div className="py-2">
              <div className="font-semibold text-xs text-stone-600 leading-none">Service Address</div>
              <div className="font-semibold text-stone-800 leading-none">{item.service_address}</div>
            </div>
            <div className="py-2">
              <div className="font-semibold text-xs text-stone-600 leading-none">End Date</div>
              <div className="font-semibold text-stone-800 leading-none">{item.end_date}</div>
            </div>
          </div>
    </SwiperSlide>)
  } 

  return (
    <Swiper
      loop={true}
      slidesPerView={1}
      spaceBetween={16}
      breakpoints={{
        577: {
          slidesPerView: 2,
        }
      }}
      modules={[Pagination]}
    >
      {mapSubscriptionCards(subscriptions)}
    </Swiper>
  )
})

export default SubsSlider;