import { Children, cloneElement, memo, useCallback, useEffect, useRef, useState } from "react";
import "./Select.scss";

const Select = memo(function Select({
  children,
  icon,
  id,
  label,
  onChange,
  value
}) {
  const realSelectRef = useRef(null);
  const selectContainerRef = useRef(null);
  const [selectContent, setSelectContent] = useState();
  const [selectValue, setSelectValue] = useState(value);
  const [toggleOptions, setToggleOptions] = useState(false);

  useEffect(() => {
    realSelectRef.current && setSelectContent(realSelectRef.current.options[realSelectRef.current.options.selectedIndex].text);
  }, [realSelectRef]);

  useEffect(() => {
    if (selectContainerRef) {
      const handleOutsideClick = (e) => selectContainerRef.current && !selectContainerRef.current.contains(e.target) && setToggleOptions(false);
      document.addEventListener("click", (e) => handleOutsideClick(e));
      return () => document.removeEventListener("click", (e) => handleOutsideClick(e));
    }
    
  }, []);

  const handleSelect = (e) => {
    const { value, text } = e.target;
    setSelectValue(value);
    setSelectContent(text);
    setToggleOptions(false);
    onChange(e);
  };

  const mapOptions = () => {
    return Children.map(children, (child, index) => {
      if (child) {
        return cloneElement(child, { key: index, handleSelect: handleSelect });
      }
    });
  };

  return (
    <div ref={selectContainerRef} className="zui-select-container">
      <div
        className={["zui zui-select", toggleOptions ? "zui-select--focused" : ""].join(" ")}
        onClick={() => (toggleOptions ? setToggleOptions(false) : setToggleOptions(true))}
      >
        <div className="zui-select-value-container">
          {icon ? <span className="mr-1">{icon}</span> : <></>}
          
          {selectContent}
        </div>
        <div className="zui-select-icon-container">
          <i className="fa-solid fa-caret-down"></i>
        </div>
      </div>
      <label className="zui-select-label" htmlFor={id}>
        {label}
      </label>
      <div className="zui-select-options">{mapOptions()}</div>
      <select ref={realSelectRef} className="hidden" onChange={(e) => setSelectValue(e.target.value)} value={selectValue}>
        {children}
      </select>
    </div>
  );
});

const Option = memo(function Option({ children, value, defaultValue, handleSelect }) {
  const handleOption = (e) => {
    handleSelect(e)
  }

  return (
    <option className={"zui zui-select-option"} onClick={ handleOption } defaultValue={ defaultValue } value={ value } >{ children }</option>
  )
})

export { Select, Option };
