import { memo } from "react";

const zuiButton = "min-w-[50px] p-4 font-semibold text-lg leading-none duration-200 user-select-none disabled:cursor-not-allowed";
const zuiButtonPrimary = "bg-red-400 text-white hover:bg-red-500 disabled:bg-red-900";
const zuiButtonPrimaryReversed = "bg-neutral-900 text-white hover:bg-red-500 disabled:bg-red-900";

const Button = memo(function Button({
  children,
  className,
  disabled,
  full,
  id,
  onClick,
  primary,
  primaryReversed,
  roundedFull,
  type = "button",
}) {
  const customClassName = [
    "zui zui-button",
    zuiButton,
    primary ? zuiButtonPrimary : "",
    primaryReversed ? zuiButtonPrimaryReversed : "",
    full ? "w-full" : "",
    roundedFull ? "rounded-full" : "rounded-lg",
    className,
  ].join(" ").replace(/ +/g, ' ');

  return (
    <button
      className={customClassName}
      disabled={disabled}
      id={id}
      onClick={onClick}
      type={type}
    >
      {children}
    </button>
  );
});

export default Button;
