import { Link, Outlet, useLocation, useNavigate } from "react-router-dom";
import ZUI from "../../../components/zui/zui";
import logo from "../../images/logo.svg";
import { Fragment, useEffect, useState } from "react";
import CustomSwal from "../../../components/Swal/Swal";
import "./MainLayout.scss";
import Breadcrumb from "./Breadcrumb";
import { useSelector } from "react-redux";

const menuItems = [
  {
    name: "Subscriptions",
    url: "subscriptions",
    icon: <i className="fa-solid fa-box" />,
  },
  {
    name: "Bills & Payments",
    icon: <i className="fa-solid fa-receipt" />,
    children: [
      {
        name: "Payment Options",
        url: "payment-options",
        icon: <i className="fa-solid fa-credit-card fa-sm" />,
      },
      {
        name: "Invoices & Payment",
        url: "invoices-and-payment",
        icon: <i className="fa-solid fa-file-invoice-dollar" />,
      },
    ]
  },
  {
    name: "Support",
    icon: <i className="fa-regular fa-message" />,
    children: [
      {
        name: "Tickets",
        url: "tickets",
        icon: <i className="fa-solid fa-ticket" />,
      },
    ]
  },
]

const navItemClassName = "nav-item group flex gap-2 relative py-4 text-sm text-left text-white hover:text-red-500 [&>.nav-item-chevron]:hover:rotate-90 [&>.nav-submenu]:hover:block"
const navSubMenuClassName = "nav-submenu absolute left-[-2px] top-[calc(100%)] hidden w-[180px] py-2 rounded-lg bg-neutral-950 z-[999]"
const navSubMenuItemClassName = "nav-item group flex gap-2 relative px-4 py-3 text-sm text-left text-white hover:text-red-500"

const MainLayout = () => {
  const [ipLocation] = useState(localStorage.getItem("ipLocation"));
  const navigate = useNavigate();
  const breadcrumb = useSelector(state => state.layout.details)

  const mapMenuItems = (items, submenuItem = false) => {
    return items?.map((item, index) => {
      if (item.url) {
        return (
          <Link key={index} className={submenuItem ? navSubMenuItemClassName : navItemClassName} to={`/${ipLocation}/${item.url}`}>
            <div className="w-3 text-right"><span >{item.icon}</span></div>
            <div>{item.name}</div>
          </Link>
        )
      } else {
        return (
          <Fragment key={ index }>
            <button className={navItemClassName}>
              <div className="w-3 text-right"><span>{item.icon}</span></div>
              <div>{item.name}</div>
              <div className="nav-item-chevron ml-2 mt-0.5 duration-100"><span className=""><i className="fa-solid fa-chevron-right fa-xs" /></span></div>
              <div className={navSubMenuClassName}>
                {mapMenuItems(item.children, true)}
              </div>
            </button>
          </Fragment>
        )
      }
    })
  }

  const handleLogout = () => {
    CustomSwal({
      text: "Are you sure you want to log out?",
      confirmButtonText: "Logout",
      showCancelButton: true,
      cancelButtonText: "Cancel",
      reverseButtons: true,
      customClass: {
        popup: "swal2-custom"
      }
    }).then((res) => {
      if (res.isConfirmed) {
        navigate(`/${ipLocation}/login`)
      }
    })
  }

  return (
    <div className="main-layout flex flex-col w-full h-full">
      <header className="flex flex-col items-center w-full py-8">
        <div className={`w-full ${breadcrumb ? "h-[270px]" : "h-[360px]"} bg-black absolute top-0 left-0 z-[-1] duration-300 delay-100`} />
        <div className="w-full max-w-7xl px-4">
          <div className="flex items-center">
            <div className="max-w-[120px]">
              <Link to={`/${ipLocation}/dashboard`}>
                <img className="invert" src={ logo } alt="logo" />
              </Link>
            </div>
            <div className="nav-container hidden md:flex gap-8 lg:gap-12 ml-8 mr-8 lg:ml-16 lg:mr-16">
              {mapMenuItems(menuItems)}
            </div>
            <div className="flex gap-2 ml-auto">
              <ZUI.Button id="profileBtn" primaryReversed>
                <span>
                  <svg xmlns="http://www.w3.org/2000/svg" height="18" width="18" viewBox="0 0 448 512" className="fill-white">
                    <path id="headPath" d="M224 256A128 128 0 1 0 224 0a128 128 0 1 0 0 256z"/>
                    <path d="M178.3 304C79.8 304 0 383.8 0 482.3C0 498.7 13.3 512 29.7 512H418.3c16.4 0 29.7-13.3 29.7-29.7C448 383.8 368.2 304 269.7 304H178.3z"/>
                  </svg>
                </span>
              </ZUI.Button>
              <ZUI.Button id="logoutBtn" primaryReversed onClick={ handleLogout }>
                <span>
                  <svg xmlns="http://www.w3.org/2000/svg" height="18" width="18" viewBox="0 0 512 512" className="fill-white">
                    <path id="arrowPath" d="M377.9 105.9L500.7 228.7c7.2 7.2 11.3 17.1 11.3 27.3s-4.1 20.1-11.3 27.3L377.9 406.1c-6.4 6.4-15 9.9-24 9.9c-18.7 0-33.9-15.2-33.9-33.9l0-62.1-128 0c-17.7 0-32-14.3-32-32l0-64c0-17.7 14.3-32 32-32l128 0 0-62.1c0-18.7 15.2-33.9 33.9-33.9c9 0 17.6 3.6 24 9.9z"/>
                    <path d="M160 96L96 96c-17.7 0-32 14.3-32 32l0 256c0 17.7 14.3 32 32 32l64 0c17.7 0 32 14.3 32 32s-14.3 32-32 32l-64 0c-53 0-96-43-96-96L0 128C0 75 43 32 96 32l64 0c17.7 0 32 14.3 32 32s-14.3 32-32 32z"/>
                  </svg>
                </span>
              </ZUI.Button>
            </div>
          </div>
        </div>
        <ZUI.Container>
          <Breadcrumb />
        </ZUI.Container>
      </header>
      <main>
        <Outlet />
      </main>
      <footer className="pt-16 pb-8 mt-auto">
        <div className="text-center">
          <span className="text-red-400">© { new Date().getFullYear() } ZGI. All Rights Reserved.</span>
        </div>
      </footer>
    </div>
  )
}

export default MainLayout;