import { StrictMode, cloneElement, useEffect, useState } from 'react';
import ReactDOM from 'react-dom/client';
import './index.scss';
import routes from './routes';
import LoginPage from './pages/Login/LoginPage';
import { HashRouter, Route, Routes, useNavigate } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import MainLayout from './assets/layouts/Main/MainLayout';
import NotFoundPage from './pages/NotFound/NotFoundPage';
import { Provider } from 'react-redux';
import store from './store/store';

const ipLocations = [
  "ph", "sg", "my", "th"
]

const App = () => {
  const [ipLocation, setIPLocation] = useState(localStorage.getItem("ipLocation") ?? "ph")

  const mapRoutes = (routes) => {
    return routes.map((route, index) => {
      return (
        <Route key={ index } exact index={route.index ?? false} path={route.path} Component={ () => route.component && cloneElement(route.component, { routeId:route.id, routePathId:route.routeId, routeTitle:route.title }) }>
          {route.children && mapRoutes(route.children)}
        </Route>
      )
    })
  }

  const PseudoComponent = () => {
    const navigate = useNavigate();
    useEffect(() => {
      navigate(`${ipLocation}/login`)
    }, [])
    

    return <div />
  }

  return (
    <HashRouter basename={process.env.REACT_APP_PRODUCTION ? `/customer-portal-react/${ipLocation}` : ``}>
      <Routes>
        <Route path="" element={ <PseudoComponent /> }  />
        <Route path={`${ipLocation}/login`} element={ <LoginPage ipLocations={ipLocations} ipLocation={ipLocation} setIPLocation={setIPLocation} /> }  />
        <Route path={ipLocation} element={ <MainLayout /> }>
          { mapRoutes(routes) }
        </Route>
        <Route path="*" element={ <NotFoundPage /> } />
      </Routes>
    </HashRouter>
  )
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
    <ToastContainer hideProgressBar={ true } />
  </StrictMode>
);