import bg from './../../assets/images/bg.webp'
import logo from './../../assets/images/logo.svg'
import LoginForm from './components/LoginForm';
import ZUI from '../../components/zui/zui';
import { useParams, useNavigate } from 'react-router-dom';
import { useState } from 'react';

const LoginPage = ({
  ipLocations,
  ipLocation,
  setIPLocation
}) => {
  const navigate = useNavigate();

  const handleIPLocationChange = (e) => {
    const { value } = e.target
    localStorage.setItem("ipLocation", value);
    setIPLocation(value);
    navigate(`/${value}/login`);
  }

  return (
    <div className="login-page flex flex-col w-full h-full bg-top bg-cover" style={{ backgroundImage: `url(${bg})` }} >
      <header className="flex justify-center w-full py-4">
        <div className="w-full max-w-7xl px-4">
          <div className="flex justify-between items-center w-full mb-8">
            <div className="w-full max-w-[90px] sm:max-w-[120px]">
              <img className="w-full invert" src={logo} alt="logo"/>
            </div>
            <div>
              <ZUI.Select value={ipLocation} onChange={ handleIPLocationChange } icon={<i className="fa-solid fa-earth-asia" />} >
                { ipLocations?.map((location, index) => <ZUI.Option key={index} value={location}>{location.toUpperCase()}</ZUI.Option>) }
              </ZUI.Select>
            </div>
          </div>
          <div>
            <div className="mb-2 min-[365px]:mb-4">
              <h1 className="font-semibold text-[calc(10.55vw)] min-[501px]:text-[calc(10.976vw)] min-[657px]:text-[72px] text-white text-center leading-none">Lorem Ipsum Dolor</h1>
            </div>
            <p className="leading-none text-center text-white min-[365px]:text-[18px]">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Fusce vel vestibulum metus.
            </p>
          </div>
        </div>
      </header>
      <main className="flex flex-col items-center w-full h-full py-8">
        <LoginForm ipLocation={ipLocation} />
      </main>
      <footer className="py-4">
        <div className="text-center">
          <span className="text-white">© { new Date().getFullYear() } ZGI. All Rights Reserved.</span>
        </div>
      </footer>
    </div>
  )
}

export default LoginPage;