import { memo, useState } from "react";

const zuiInput = "w-full px-3 pt-5 pb-2 rounded-lg bg-neutral-100 font-semibold text-xl text-neutral-500 caret-neutral-400 [&[type=password]]:tracking-[6px]"
const zuiInputHover = "hover:bg-neutral-200"
const zuiInputFocus = "focus:outline focus:outline-2 focus:outline-neutral-300 focus focus:bg-neutral-200 [&+.zui-input-label]:focus:top-1 [&+.zui-input-label]:focus:text-xs"
const zuiInputNotPlaceholderShown = "[&:not(:placeholder-shown)+.zui-input-label]:top-1 [&:not(:placeholder-shown)+.zui-input-label]:text-xs"
const zuiInputDisabled = "zui-input--disabled disabled:bg-neutral-200 disabled:cursor-not-allowed"
const zuiInputError = "zui-input--error [&.zui-input--error]:outline [&.zui-input--error]:outline-2 [&.zui-input--error]:outline-red-400 [&.zui-input--error]:bg-neutral-200 [&.zui-input--error]:caret-red-400"
const zuiLabel = "absolute left-3 top-1/4 text-lg text-neutral-400 duration-100 user-select-none pointer-events-none"
const zuiLabelError = "zui-input-label--error [&.zui-input-label--error]:top-1 [&.zui-input-label--error]:text-xs [&.zui-input-label--error]:text-red-400"

const Input = memo(function Input({
  autoComplete = "off",
  disabled = false,
  id,
  label,
  onChange,
  required = false,
  type = "text",
  uppercase = false,
  value,
}) {
  const [errorInput, setErrorInput] = useState(false);

  const customClassName = [
    "zui zui-input",
    zuiInput,
    zuiInputHover,
    zuiInputFocus,
    zuiInputNotPlaceholderShown,
    zuiInputDisabled,
    uppercase ? "uppercase" : "",
    errorInput ? zuiInputError : ""
  ].join(" ").replace(/ +/g, ' ')

  const handleInput = () => setErrorInput(false);
  const handleInvalid = () => setErrorInput(true);

  const customLabelClassName = [
    "zui zui-input-label",
    zuiLabel,
    errorInput ? zuiLabelError : "",
  ].join(" ").replace(/ +/g, ' ')

  return (
    <div className="zui zui-input-container relative">
      <input
        autoComplete={autoComplete}
        className={customClassName}
        disabled={disabled}
        id={id}
        onChange={onChange}
        onInput={handleInput}
        onInvalid={handleInvalid}
        placeholder=" "
        required={required}
        type={type}
        value={value}
      />
      <label className={customLabelClassName} htmlFor={id}>
        {errorInput ? 'Error' : label}
      </label>
    </div>
  );
});

export default Input;
