import { Link } from "react-router-dom";

const NotFoundPage = () => {

  return (
    <div className="not-found-page w-full">
      <div className="flex justify-center items-center h-full">
        <div>
          <div className="text-center">
            <span className="font-semibold font-mono text-8xl text-neutral-300 leading-none">404</span>
          </div>
          <div className="text-center">
            <span className="font-semibold text-2xl text-neutral-400 leading-none">Page Not Found</span>
          </div>
          <div className="mt-4 text-center">
            <Link to={-1} className="text-neutral-500 hover:text-neutral-700">
              <span className="mr-2"><i className="fa-solid fa-arrow-left-long fa-sm" /></span>
              <span>Return</span>
            </Link>
          </div>
        </div>
      </div>
    </div>
  )
}

export default NotFoundPage;