import ZUI from "../../components/zui/zui";
import profile from "../../assets/images/placeholder_profile.webp";
import { Link, useParams } from "react-router-dom";
import { Fragment, useEffect, useRef, useState } from "react";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import SubsSlider from "./components/SubsSlider";
import image1 from "../../assets/images/DSC06654.webp";
import image2 from "../../assets/images/DSC06679.webp";
import image3 from "../../assets/images/DSC06689.webp";
import { Swiper, SwiperSlide } from "swiper/react";
import Ad from "./components/Ad/Ad";
import { Autoplay } from "swiper/modules"
import { useDispatch } from "react-redux";
import { setDetails } from "../../store/reducers/layoutSlice";

const profileDetails = [
  {
    icon: <i className="fa-solid fa-user fa-lg" />,
    label: "Name",
    value: "Juan Dela Cruz",
  },
  {
    icon: <i className="fa-solid fa-phone fa-lg" />,
    label: "Phone Number",
    value: "+63 912 234 5678",
  },
  {
    icon: <i className="fa-solid fa-archive fa-lg" />,
    label: "Account Number",
    value: "T00001234",
  },
  {
    icon: <i className="fa-solid fa-envelope fa-lg" />,
    label: "Email",
    value: "juandc@email.com",
  },
]

export const subscriptions = [
  {
    sub_id: "SUB-00001234",
    plan_name: "Plan 999",
    service_address: "123 Estrit St 1 #12-34 (123456)",
    end_date: "01-02-2033"
  },
  {
    sub_id: "SUB-00005678",
    plan_name: "Plan 1999",
    service_address: "234 Abennew Ave 2 #23-45 (234567)",
    end_date: "02-03-2033"
  },
  {
    sub_id: "SUB-00001324",
    plan_name: "Plan 2999",
    service_address: "345 Rowd Rd 3 #34-56 (345678)",
    end_date: "03-04-2033"
  },
  {
    sub_id: "SUB-00005768",
    plan_name: "Plan 1999",
    service_address: "456 Haywei Hwy 4 #45-67 (456789)",
    end_date: "04-05-2033"
  },
  {
    sub_id: "SUB-00009876",
    plan_name: "Plan 3999",
    service_address: "567 Boylee Bard Blv 5 #56-78 (567890)",
    end_date: "05-06-2033"
  },
]

const ads = [
  image1, image2, image3
]

const currencyCode = () => {
  if (document.location.pathname.split("/")[1] === "ph") return "PHP";
  if (document.location.pathname.split("/")[1] === "sg") return "SGD";
  if (document.location.pathname.split("/")[1] === "my") return "MYR";
  if (document.location.pathname.split("/")[1] === "th") return "THB";

  return "PHP";
}

const DashboardPage = () => {
  const dispatch = useDispatch();
  const [ipLocation] = useState(localStorage.getItem("ipLocation"));

  useEffect(() => {
    dispatch(setDetails())
  }, [])

  const mapProfileDetails = (items) => {
    return items.map((item, index) => {
      return (
      <div key={index} className="col-span-1 flex items-center gap-4">
        <div>
          <div className="flex justify-center items-center w-[40px] h-[40px] rounded-lg bg-red-400">
            <span className="text-white">{item.icon}</span>
          </div>
        </div>
        <div className="flex flex-wrap">
          <div className="w-full">
            <span className="font-semibold text-sm text-stone-500 leading-none align-bottom">{item.label}</span>
          </div>
          <div className="w-full">
            <span className="font-semibold text-stone-600 leading-none align-top">{item.value}</span>
          </div>
        </div>
      </div>
      )
    })
  }

  const mapSubscriptions = (items) => {
    return items.map((item, index) => <Fragment key={index}>
      <div className="py-2 font-semibold">{item.sub_id}</div>
      <div className="py-2 font-semibold">{item.plan_name}</div>
      <div className="py-2 font-semibold">{item.service_address}</div>
      <div className="py-2 font-semibold">{item.end_date}</div>
    </Fragment>)
  }

  const mapAds = (items) => {
    return items.map((item, index) => <SwiperSlide key={index} className="h-full">
      <Ad ad_image={item} />
    </SwiperSlide>)
  }

  return (
    <div className="dashboard-page flex flex-col w-full h-full">
      <ZUI.Row>
        <ZUI.Container className="items-center">
          <div className="w-[90px] md:w-[120px] rounded-full overflow-hidden">
            <img src={profile} alt="profile" />
          </div>
          <div className="ml-4 md:ml-8">
            <span className="font-semibold text-4xl md:text-6xl text-white">Hi, Juan</span>
          </div>
        </ZUI.Container>
      </ZUI.Row>
      <ZUI.Row className="mt-4">
        <ZUI.Container>
          <div>
            <span className="font-semibold text-xl text-white">Dashboard</span>
          </div>
        </ZUI.Container>
      </ZUI.Row>
      <ZUI.Row className="mt-4">
        <ZUI.Container>
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 w-full">
            <div className="col-span-1 sm:col-span-2 px-6 pt-4 pb-6 rounded-2xl bg-red-50">
              <div className="mb-2">
                <Link to={`/${ipLocation}/profile`} className="flex justify-between">
                  <span className="font-semibold text-lg text-red-400">Profile Details</span>
                  <span className="font-semibold text-lg text-red-400"><i className="fa-solid fa-chevron-right fa-sm"/></span>
                </Link>
              </div>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-1 sm:gap-3 w-full">
                {mapProfileDetails(profileDetails)}
              </div>
            </div>
            <div className="col-span-1 flex flex-col justify-between min-h-[184px] px-6 pt-4 pb-6 rounded-2xl bg-red-50">
              <div className="mb-2">
              <Link to={`/${ipLocation}/tickets`} className="flex justify-between">
                  <span className="font-semibold text-lg text-red-400">Tickets</span>
                  <span className="font-semibold text-lg text-red-400"><i className="fa-solid fa-chevron-right fa-sm"/></span>
                </Link>
              </div>
              <div className="grid grid-cols-[auto_1fr] gap-x-2 w-full mb-4">
                <div className="text-right">
                  <span className="font-mono font-semibold text-4xl text-stone-900">1</span>
                </div>
                <div>
                  <span className="font-semibold text-3xl text-stone-600">Open</span>
                </div>
                <div className="text-right">
                  <span className="font-mono font-semibold text-4xl text-stone-900">999</span>
                </div>
                <div>
                  <span className="font-semibold text-3xl text-stone-600">Closed</span>
                </div>
              </div>
            </div>
            <div className="col-span-1 flex flex-col justify-between min-h-[184px] px-6 pt-4 pb-6 rounded-2xl bg-red-50">
              <div className="mb-2">
                <Link to={`/${ipLocation}/invoices-and-payment`} className="flex justify-between">
                  <span className="font-semibold text-lg text-red-400">Current Balance</span>
                  <span className="font-semibold text-lg text-red-400"><i className="fa-solid fa-chevron-right fa-sm"/></span>
                </Link>
              </div>
              <div className="mb-4">
                <div className="text-ellipsis overflow-hidden">
                  <span className="font-mono font-semibold text-4xl text-stone-900" title="1,234,567.89">1,234,567.89</span>
                  <span className="font-semibold text-xl text-stone-600 text-right"> { currencyCode() }</span>
                </div>
              </div>
            </div>
            <div className="col-span-1 sm:col-span-2 lg:col-span-4 xl:col-span-5">
              <Swiper
                loop={true}
                slidesPerView={1}
                slidesPerGroup={1}
                spaceBetween={16}
                centeredSlides={true}
                speed={3000}
                onAutoplayResume={function() {
                  this.params.speed = 3000;
                  this.update();
                }}
                onTouchStart={function() {
                  this.params.speed = 300;
                  this.update();
                }}
  
                autoplay={{
                  delay: 3000,
                }}
                modules={[Autoplay]}
                className="h-full rounded-2xl"
              >
                {mapAds(ads)}
              </Swiper>
            </div>
          </div>
        </ZUI.Container>
      </ZUI.Row>
      <ZUI.Row className="mt-4">
        <ZUI.Container>
          <div className="w-full md:p-6 md:rounded-2xl mt-8 md:mt-0 bg-transparent md:bg-red-50">
            <div className="mb-2">
              <Link to={`/${ipLocation}/subscriptions`} className="flex justify-between">
                <span className="font-semibold text-lg text-red-400">Subscriptions</span>
                <span className="font-semibold text-lg text-red-400"><i className="fa-solid fa-chevron-right fa-sm"/></span>
              </Link>
            </div>
            <div className="hidden md:block">
              <div className="grid grid-cols-[minmax(125px,_1fr)_minmax(110px,_1fr)_3fr_minmax(90px,_1fr)] gap-4 px-8 py-2 rounded-full mb-4 bg-red-400">
                <div className="font-semibold text-white">Subscription ID</div>
                <div className="font-semibold text-white">Plan Name</div>
                <div className="font-semibold text-white">Service Address</div>
                <div className="font-semibold text-white">End Date</div>
              </div>
              <div className="grid grid-cols-[minmax(125px,_1fr)_minmax(110px,_1fr)_3fr_minmax(90px,_1fr)] gap-4 px-8">
                {mapSubscriptions(subscriptions)}
              </div>
            </div>
            <div className="block md:hidden">
              <SubsSlider subscriptions={subscriptions} />
            </div>
          </div>
        </ZUI.Container>
      </ZUI.Row>
    </div>
  )
}

export default DashboardPage;